<template>
    <Header :homeDisplay="this.homeDisplay" />    
    
    <div class="main_display">        
        <b-button pill variant="primary" @click="changeMap">{{this.nextmap}}으로 변경</b-button>            
        <div class="map_display">        
            <KakaoMap     v-if="this.mapindex==0"/> 
            <NaverMap     v-if="this.mapindex==1"/>
            <OpenLayerMap v-if="this.mapindex==2"/>
            <GoogleMap    v-if="this.mapindex==3"/>
        </div>

    </div>

</template>

<script>
import {ref} from 'vue';
import Header from "@/components/Header.vue";
const homeDisplay = ref(false);
import KakaoMap from "@/components/KakaoMap";
import NaverMap from "@/components/NaverMap";
import GoogleMap from "@/components/GoogleMap";
import OpenLayerMap from "@/components/OpenLayerMap"

// import oleum from "../assets/jeju_oleum.json"
// import olle from "../assets/jeju_olle_trail.json"
// import halla from "../assets/jeju_halla_trail.json"
// import hiking from "../assets/jeju_hiking_total.json"

// import { ref } from 'vue'
// import { useElementSize } from '@vueuse/core'

// var init_height = '380px';
// var init_width = '350px'

export default{
    name: "HomeView",

    components: {
        Header,
        KakaoMap,
        NaverMap,
        GoogleMap,
        OpenLayerMap,
    },

    data() {
        return{
            homeDisplay:homeDisplay,
            mapindex: 0,
            nextmap:'네이버맵',
            // height: init_height,
            // width: init_width,
            // oleum_data: oleum,
            // olle_data: olle,
            // halla_data: halla,
            // hiking_data: hiking,
        }
    },

    methods:{

        changeMap() {            
            
            this.mapindex++;           
            
            if (this.mapindex==0) { this.nextmap='네이버맵'}
            if (this.mapindex==1) { this.nextmap='오픈맵'}
            if (this.mapindex==2) { this.nextmap='구글맵'}
            if (this.mapindex==3) { this.nextmap='카카오맵'}
            
            if (this.mapindex > 3) {
                this.mapindex=0;
                this.nextmap='네이버맵';
            }
        },
    }

}
</script>

<style scoped>
.main_display {
    padding: 5px;
}
.map_display {
    padding-top : 3px;
}
</style>

