<template>

    <div id="map">
        <GoogleMap
        :api-key="apikey"
        style="width: 100%; height: 100%"
        :center="center"
        :zoom="10"
        >
            <!-- <Marker :options="{ position: center }" /> -->
        </GoogleMap>
    </div>

</template>

<script>
// import { defineComponent } from 'vue'
import { GoogleMap, Marker } from 'vue3-google-map'
// import {ref} from 'vue';

// var apikey= ref('');

export default {    
    components: { 
        GoogleMap, 
        Marker 
    },

    created() {
        // console.log(process.env.VUE_APP_GOOGLEMAP_API_KEY);
        this.apikey = process.env.VUE_APP_GOOGLEMAP_API_KEY;
        // console.log('api_key ', this.apikey);
    },

    // props:{
    //     height:String,
    //     width:String,
    // },

    setup() {
        const center = { lat : 33.38, lng : 126.58 }
        return { center }
    },

    data() {
        return {
            // mStyle: 'width:'+this.width+'; height: '+this.height,
            apikey: "",
        }
    }
}
</script>